<template>
  <div class="musicList">
    <div class="bg-layer" ref="bgLayer">
    <div class="infoBox" >
      <div class="background">
        <img :src="bgImage" alt="">
      </div>
      <div class="topBar">
        <i class="iconfont icon-fanhui" @click="goback"></i>
      </div>
      <div class="album">
        <div class="albumImage">
        <img :src="bgImage" alt="">
        </div>
        <div class="albumBd">
          <h1 v-html="title"></h1>
          <p v-text="'单曲:'+songsLength+'首'" v-show="songsLength"></p>
        </div>
      </div>
      <div class="opt_box">
        <div class="back">
          <i class="iconfont icon-fanhui" @click="goback" v-show="-scrollY>150"></i>
        </div>
        <div class="box">
        <a href="javascript:;" @click="random"><i class="iconfont icon-suijibofang"></i>随机播放全部</a>
        </div>
        <div class='back2'></div>
      </div>
    </div>
    </div>
    <Scroll :data="songs" class="list" :probeType="probeType" :listenScroll="listenScroll" @scroll="scroll" ref="songs">
      <div class="song-wrapper" ref="songsWapper">
        <songList :songs="songs" @select="selectItem" :rank="rank"></songList>
      </div>
      <div class="loading-container" v-show="!songs.length">
        <loading></loading>
      </div>
      <transition name="backs">
      <div class="btn" @click="toTop" v-show="-scrollY>500">
        <i class="iconfont icon-Group-"></i>
      </div>
        </transition>
    </Scroll>
    </div>
</template>

<script>
  import Scroll from '../../base/scroll'
  import songList from '../../base/songList'
  import loading from '../../base/loading'
  import {mapActions} from 'vuex'
  import {playlistMixin} from '../../common/js/mixin'
  export default {
    mixins:[playlistMixin],
   props:{
     bgImage:{
       type:String,
       default:''
     },
     songs:{
       type:Array,
       default: []
     },
     title:{
       type:String,
       default:''
     },
     songsLength:{
       type:Number,
       default:0
     },
     rank:{
       type:Boolean,
       default:false
     }
   },
   created(){
     this.probeType =3;
     this.listenScroll = true
   },
   methods:{
     goback(){
       this.$router.go(-1)
     },

     handlePlayList(playList){
        const bottom =playList.length>0 ? '66px':''
       this.$refs.songs.$el.style.bottom = bottom
       this.$refs.songs.refresh()
     },
     random(){
       this.randomPlay({
         list:this.songs
       })
     },
     scroll(pos){
       this.scrollY = pos.y
     },
     selectItem(item,index){
        this.selectPlay({
          list:this.songs,
          index
        })
     },
     toTop(){
       console.log(this.scrollY)
       this.$nextTick(()=>{
         this.$refs.songs.scrollTo(0,0,500)
       })

     },
     ...mapActions([
       'selectPlay',
       'randomPlay'
     ])
   },
   watch:{
     scrollY(newY) {
       if(newY<0){
       let translateY = Math.max(-191, newY)
       this.$refs.bgLayer.style['transform'] = `translate3d(0,${translateY}px,0)`
         this.$refs.bgLayer.style['webkitTransform'] = `translate3d(0,${translateY}px,0)`
       }
     //   if(newY>-191&&newY<0){
     //   this.$refs.bgLayer.style['transform']=`translate3d(0,${newY}px,0)`
     // }
     }
   },
   data(){
     return{
       scrollY:0
     }
   },
   components:{
     Scroll,
     songList,
     loading
   }
 }
</script>

<style scoped lang="stylus">
  @import "../../common/stylus/mixin.styl"
    .musicList{
      position: absolute
      top:0
      left:0
      bottom:0
      right:0
      z-index: 60
      background-color:#fff
      .bg-layer{
        position: absolute
        top:0
        left:0
        width: 100%
        height :275px
        color: #FFFFFF
        z-index:5
        background: rgba(255,255,255,1)
        overflow :hidden
        .background{
          position: absolute
          top:0
          left:0
          z-index: -1
          width: 100%
          height:100%
          background:rgba(0,0,0,.5)

          img{
            width:100%
            height:100%
            filter :blur(20px)
            object-fit :cover
          }
        }
        .topBar{
          display: flex
          height :48px
          padding:0 16px 0 14px
          line-height :48px
          background:rgba(0,0,0,0.3)
          .iconfont{
            font-size :24px
          }

        }
        .album{
          display: flex
          align-items :center
          height:143px
          width:100%
          .albumImage img{
            width:125px
            height:125px
            border-radius :50%
            margin:0 20px
          }
          h1{
            height:50px
            line-height :30px
            font-size :20px
            padding-right:30px
            text-align :center
          }
          p{
            height:50px
            line-height :50px
          }
        }
        .opt_box{
          height :84px
          display :flex
          align-items :center
          justify-content :center
          .gobackto-enter-active,.gobackto-leave-active{
            transition: all .4s
          }
          .gobackto-enter,.gobackto-leave-to{
            opacity: 0
          }
          .back{
            flex:0 0 60px
            padding-left:15px
            i{
              font-size :24px
            }

          }
          .back2{
            flex:0 0 60px
          }
          .box{
            flex:1
            text-align :center
            display:flex
            justify-content :center
          }
          i{
            height:40px
            line-height :40px
            font-size :20px
            padding-right :5px
            margin-top:1px
          }
          a{
            height:40px
            line-height :40px
            display :flex
            width:170px
            color: #000
            align-items :center
            justify-content :center
            padding:0 20px
            overflow :hidden
            border-radius :20px
            background: linear-gradient(to top, #fff1eb 0%, #ace0f9 100%);

          }

        }
      }
      .list{
        position: absolute
        top:275px
        width: 100%
        bottom:0
        z-index :1
        .song-wrapper{
          position: relative
          z-index:-6
        }
      }
    }
  .loading-container
    position: absolute
    width: 100%
    top: 50%
    transform: translateY(-50%)
  .btn{
    position: fixed
    width:20px
    height:20px
    right:20px
    bottom:80px
    z-index:20

    transform :rotate(180deg)
    i{
      font-size :32px
      color:rgba(0,0,0,0.3)
    }
  }

</style>



